.food {
  display: flex;
  flex-direction: column;

  width: 320px;

  position: relative;

  text-align: center;

  background-image: url("../img/index-background-mobile@1x.jpg");
  background-repeat: no-repeat;
  background-size: 320px 305px;

  color: @white;
}

.food__heading {
  .mapa();
  margin: 30px 0 20px;

  font-size: 36px;
  font-weight: 100;
  line-height: 36px;
  letter-spacing: 0.5px;
  text-align: center;
}

.food__text {
  .mapa();

  .font-size(14px; 14px);
  font-weight: 200;
  text-transform: uppercase;
}

.food-image {
  margin: 0 auto;
  margin-top: 30px;

  display: block;
}

@media (min-resolution: @retina-dppx) {
  .food {
    background-image: url("../img/index-background-mobile@1x.jpg");
  }
}

@media (min-width: @tablet-width) {
  .food {
    width: auto;
    background-image: none;
  }

  .food__heading {
    width: 500px;

    margin-left: 90px;

    .font-size(60px; 60px);
    text-align: left;

    color: @black;
  }

  .food__text {
    margin-left: 90px;
    margin-top: 40px;

    .font-size(20px; 20px);
    text-align: left;

    color: @black;
  }

  .food-image {
    position: relative;
    z-index: 10;

    width: 709px;
    height: 609px;
  }

  .food__button {
    position: absolute;
    top: 30%;
    z-index: 10;

    margin-left: 90px;
  }

  @media (min-resolution: @retina-dppx) {
    .food {
      background-image: url("../img/index-background-tablet@2x.jpg");
    }
  }
}

@media (min-width: @desktop-width) {
  .food {
    background-image: none;

    flex-direction: row;
    justify-content: flex-start;

    &::after {
      position: absolute;
      right: -70px;
      top: -120px;
      z-index: 0;

      content: "";

      width: 720px;
      height: 696px;

      background-image: url("../img/index-background-desktop@1x.jpg");
      background-repeat: no-repeat;
      background-size: cover;
    }
  }

  .food__header {
    width: 40%;
  }

  .food__heading {
    width: 490px;

    margin-left: 80px;
    margin-top: 110px;

    .font-size(60px; 60px);
    text-align: left;

    color: @black;
  }

  .food__text {
    margin-left: 80px;
  }

  .food-image {
    width: 552px;
    height: 499px;
  }

  .food__button {
    position: absolute;
    top: 36%;

    margin-left: 80px;
    margin-top: 150px;
  }

  @media (min-resolution: @retina-dppx) {
    .food {
      background-image: url("../img/index-background-desktop@2x.jpg");
    }
  }
}
